.select {
    position: relative;
    width: 20em;
    min-height: 1.5em;
    border: .05em solid #777;
    display: flex;
    align-items: center;
    gap: .5em;
    padding: .5em;
    border-radius: .25em;
    outline: none;

    &:focus {
        border-color: hsl(200, 100%, 50%);
    }

    .value {
        flex-grow: 1;
        display: flex;
        gap: .5em;
        flex-wrap: wrap;
    }

    .filter-input,
    .placeholder {
        border: none;
        outline: none;
        flex-grow: 1;
        display: flex;
        gap: .5em;
        flex-wrap: wrap;
    }

    .placeholder {
        opacity: 0.5;
    }

    .clear-btn {
        background: none;
        color: #777;
        border: none;
        outline: none;
        cursor: pointer;
        padding: 0;
        font-size: 1.25em;

        &:hover,
        &:focus {
            color: #333;
        }
    }

    .divider {
        background-color: #777;
        align-self: stretch;
        width: .05em;
    }

    .caret {
        translate: 0 25%;
        border: .25em solid transparent;
        border-top-color: #777;
    }

    .options {
        position: absolute;
        margin: 0;
        padding: 0;
        list-style: none;
        display: none;
        max-height: 15em;
        overflow-y: auto;
        border: .05em solid #777;
        border-radius: .25em;
        width: 100%;
        left: 0;
        top: calc(100% + .25em);
        background-color: white;
        z-index: 100;

        &.show {
            display: block;
        }

        .option {
            padding: .25em .5em;
            cursor: pointer;

            &.selected {
                background-color: hsl(200, 100%, 70%);
            }

            &.highlighted {
                background-color: hsl(200, 100%, 50%);
                color: white
            }
        }
    }

    .selected-option {
        display: flex;
        align-items: center;
        border: .05em solid #777;
        border-radius: .25em;
        padding: .15em .25em;
        gap: .25em;
        cursor: pointer;
        background: none;
        outline: none;

        &>.remove-btn {
            font-size: 1.25em;
            color: #777;
        }

        &:hover,
        &:focus {
            background-color: hsl(0, 100%, 90%);
            border-color: hsl(0, 100%, 50%);

            &>.remove-btn {
                color: hsl(0, 100%, 50%);

            }

        }
    }
}